import React, { Component } from 'react';
import SmartGesture from '../../components/smart-gesture/src/';

import './index.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      result1: {},
      result2: {},
      storedPoints: null,
    };
  }

  componentDidMount() {
    this.refs.gConfirm.addEventListener('click', () => {
      this.refs.g2.addGesture({
        name: this.refs.gName.value,
        points: this.state.storedPoints,
      });
      this.refs.gName.value = '';
    });
    this.refs.gRemove.addEventListener('click', () => {
      this.refs.g1.destroy();
    });
  }

  _onGesture1(res) {
    this.setState({
      result1: res,
    });
  }

  _onGesture2(res, points) {
    this.setState({
      result2: res,
      storedPoints: points,
    });
  }

  render() {
    const options = {
      lineColor: 'red',
      lineWidth: '1',
    };

    return (
      <div className="demo">
        <h1>smart-gesture-react</h1>
        <p>
          smart-gesture for React.  web 
        </p>
        <p>
          ( ,  )
        </p>
        <hr/>
        <h5>
          example1: 
        </h5>
        <hr/>
        <p><b>:</b> {this.state.result1.name || ''} <b>:</b> {this.state.result1.score || 0}</p>

        <SmartGesture ref="g1" onGesture={this._onGesture1.bind(this)}>
          <div style={{border: '1px solid gray'}}>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </SmartGesture>
        <p>
          <button ref="gRemove">remove gesture</button>
        </p>
        <hr/>
        <p>
          example2: 
        </p>
        <hr/>
        <p><b>:</b> {this.state.result2.name || ''} <b>:</b> {this.state.result2.score || 0}</p>

        <SmartGesture ref="g2" onGesture={this._onGesture2.bind(this)} style={{width:300,height: 200}} options={options} />
        <br/>
        <p>
          : <input type="text" ref="gName"/> <button ref="gConfirm"></button> ( tip:  )
        </p>

        <br/>
        <br/>
        <br/>
        <br/>
      </div>
    );
  }
}

export default App;



